import React from "react";
import Link from "../../components/Link";

import "./styles.scss";

const StripeBuyButton = (props) => {
    const {price, stripeUrl} = props;

    const str = price ? `Buy | $${price}` : 'Pay with Stripe';
    return (
        <Link className="StripeBuyButton" href={stripeUrl} useTheming={false}>{str}</Link>
    )
}

export default StripeBuyButton;